
import { calTime, titleVal } from "../util.js";
import Group from 'vux/src/components/group/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from "vuex";
export default {
name: "Topic",

props: ["id"],

data() {
return {
topic: null,
replyContent: "",
isShowPopup: false,
reply_id: null,
reply_loginname: ""
};
},

computed: {
...mapState(["isLogined"]),
createAt() {
return ":" + calTime(this.topic.create_at);
},
tag() {
return titleVal[this.topic.tab];
}
},
components: {
Group,
XButton,
XTextarea,
FlexboxItem,
Flexbox,
WechatEmotion,
Popup
},
directives: {
TransferDom
},

beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http
.get("/topic/" + to.params.id, {
params: {
accesstoken: vm.$store.state.accesstoken
}
})
.then(function(resp) {


vm.topic = resp.data.data;
});
});
},

beforeRouteUpdate: function(to, from, next) {
this.$http
.get("/topic/" + to.params.id, {
params: {
accesstoken: this.$store.state.accesstoken
}
})
.then((resp)=> {
this.topic = resp.data.data;
});
next();
},

methods: {

addReply(replyTab) {

var params = {
accesstoken: this.$store.state.accesstoken,
content: this.replyContent
};
if (replyTab == 2) {
params.reply_id = this.reply_id;

params.content = "@" + this.reply_loginname + " " + params.content;
}

this.$http
.post("/topic/" + this.topic.id + "/replies", params)
.then(resp => {
if (resp.data && resp.data.success) {
var reply = {
id: resp.data.reply_id,
author: {
loginname: this.$store.state.loginname,
avatar_url: ""
},
content: params.content,
ups: [],
create_at: "2014-10-07",
reply_id: null,
is_uped: false
};
if (replyTab == 2) {
reply.reply_id = this.reply_id;
}
this.topic.replies.push(reply);
this.replyContent = "";
this.reply_id = null;
this.isShowPopup = false;
this.reply_loginname = "";
}
});
},

showPopup(reply_id, loginname) {
this.isShowPopup = true;
this.reply_id = reply_id;
this.reply_loginname = loginname;
},
hidePopup() {
this.reply_id = "";
this.reply_loginname = "";
},

upOrDown(reply_id) {
this.$http
.post("/reply/" + reply_id + "/ups", {
accesstoken: this.$store.state.accesstoken
})
.then(resp => {
if (resp && resp.data && resp.data.success) {
var type = resp.data.action;
var reply;

for (var i = 0; i <= this.topic.replies.length; i++) {
if (this.topic.replies[i].id == reply_id) {

reply = this.topic.replies[i];
break;
}
}

if (type == "up") {

reply.ups.push(this.$store.state.accesstoken);
}

else if (type == "down") {

reply.ups.pop();
}
}
});
},

collect() {
this.$http
.post("/topic_collect/collect", {
accesstoken: this.$store.state.accesstoken,
topic_id: this.topic_id
})
.then(resp => {
if (resp.data && resp.data.success) {
this.topic.is_collect = true;
}
});
},

decollect() {
this.$http
.post("/topic_collect/de_collect", {
accesstoken: this.$store.state.accesstoken,
topic_id: this.topic_id
})
.then(resp => {
if (resp.data && resp.data.success) {
this.topic.is_collect = false;
}
});
}
}
};



