export function filterTag (item, tagValue) {
  if (item.top) {
    return ''
  }
  if (item.good) {
    return ''
  }
  return tagValue[item.tab]
}

export const titleVal = {
  share: '',
  good: '',
  job: '',
  ask: '',
  about: '',
  topic: '',
  message: '',
  dev: ''
}

export function calTime (time) {
  let now = Date.now()
  let last = new Date(time).getTime()
  let distance = ((now - last) / 1000) >> 0
  if (distance > 12 * 30 * 24 * 60 * 60) {
    return ((distance / 12 / 30 / 24 / 60 / 60) >>> 0) + ''
  } else if (distance > 30 * 24 * 60 * 60) {
    return ((distance / 30 / 24 / 60 / 60) >>> 0) + ''
  } else if (distance > 7 * 24 * 60 * 60) {
    return ((distance / 7 / 24 / 60 / 60) >>> 0) + ''
  } else if (distance > 24 * 60 * 60) {
    return ((distance / 24 / 60 / 60) >>> 0) + ''
  } else if (distance > 60 * 60) {
    return ((distance / 60 / 60) >>> 0) + ''
  } else if (distance > 60) {
    return ((distance / 60) >>> 0) + ''
  } else if (distance < 0) {
    return ''
  } else {
    return distance + ''
  }
}



