
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState, mapGetters, mapMutations } from "vuex";

export default {
name: "TopicList",

props: ["name"],

components: {
Tab,
TabItem,
Group,
Cell,
Scroller
},

data() {
return {
lists: [],

page: 1,

limit: 15,

pullupConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: "..."
},

pulldownConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: "..."
}
};
},

computed: {
...mapState(["topics"])
},

methods: {
routeto(index) {

this.$router.push("/topiclist/" + this.topics[index].name);
},


getDescByTab(tab) {
return this.$store.getters.getTopicDescByTab(tab);
},

loadMoreData() {
this.page = this.page + 1;
this.$http
.get("/topics", {
params: {

tab: this.name,
page: this.page,
limit: this.limit
}
})
.then(resp => {
if (resp.data.success) {
for (var i = 0; i < resp.data.data.length; i++) {
this.lists.push(resp.data.data[i]);
}


this.$refs.pullup.donePullup();
}
});
},

freshData() {
this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit
}
})
.then(resp => {
if (resp.data && resp.data.success) {
this.lists = resp.data.data;
this.$refs.pullup.donePulldown();
}
});
}
},

beforeRouteEnter(to, from, next) {




next(function(vm) {
vm.$http
.get("/topics", {
params: {
tab: to.params.name,
page: vm.page,
limit: vm.limit
}
})
.then(resp => {
vm.lists = resp.data.data;
});
});
},

beforeRouteUpdate(to, from, next) {

this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(resp => {
if (resp.data.success) {

this.lists = resp.data.data;
}
});

next();
}
};



